.filter {
  &__title {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.4;
  }

  &__group {
    padding: 16px 24px 24px;

    &:not(:last-child) {
      border-bottom: 1px solid #D9D9D9;
    }
  }

  &__items {
    display: flex;
  }

  &__item {
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 16px;
    }

    input {
      display: none;

      &:checked ~ .filter__text {
        border-color: $accent;
      }
    }

    &:hover {
      .filter__text {
        color: $accent;
      }
    }
  }

  &__text {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 41px;
    height: 31px;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 6px;
    font-weight: 500;
    font-size: 14px;
    color: #686868;
    transition: border-color 0.3s, color 0.3s;
  }

  &__checkbox {
    display: flex;
    align-items: center;
    font-weight: 500;
    color: #686868;
    cursor: pointer;
    transition: color 0.3s;

    &:hover {
      color: $accent;
    }

    &:not(:last-child) {
      margin-bottom: 22px;
    }

    input {
      display: none;

      &:checked ~ .filter__icon {
        border-color: $accent;

        &::after {
          opacity: 1;
          transform: scale(1);
        }
      }
    }
  }

  &__icon {
    position: relative;
    width: 30px;
    height: 30px;
    margin-right: 19px;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 6px;
    transition: border-color 0.3s;

    &::after {
      content: "";
      position: absolute;
      top: calc(50% - 6px);
      left: calc(50% - 7px);
      display: block;
      width: 14px;
      height: 11px;
      background-image: url("../img/tick-orange.svg");
      background-position: center center;
      background-size: contain;
      background-repeat: no-repeat;
      opacity: 0;
      transform: scale(0.5);
      transition: opacity 0.3s, transform 0.3s;
    }
  }

  @include max(1650) {
    &__group {
      padding: 16px 14px 18px;
    }

    &__item {
      &:not(:last-child) {
        margin-right: 8px;
      }
    }

    &__checkbox {
      font-size: 14px;

      &:not(:last-child) {
        margin-bottom: 14px;
      }
    }
  }
}
