.scr1 {
  position: relative;
  min-height: 100vh;
  padding-top: 226px;
  padding-bottom: 55px;
  overflow: hidden;
  z-index: 1;

  &__inscr {
    margin-bottom: 16px;
  }

  &__content {
    max-width: 745px;
  }

  &__info {
    margin-bottom: 114px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.388; /* 50 / 36 */

    strong {
      font-weight: 700;
      font-size: 48px;
    }

    b {
      display: block;
      font-weight: 500;
      margin-top: 10px;
    }
  }

  &__bigTxt {
    text-transform: uppercase;
  }

  &__btns {
    margin-bottom: 79px;
  }

  // &__features {
  //   max-width: 1071px;
  // }

  &__img {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    max-width: none;
    z-index: -1;
  }

  &__house {
    position: absolute;
    right: -300px;
    bottom: -30px;
    width: 1660px;
    z-index: -1;
  }

  @include max(1650) {
    padding-top: 196px;
    padding-bottom: 55px;

    // &__features {
    //   display: none;
    // }

    &__title {
      font-size: 26px;

      strong {
        font-size: 37px;
      }

      b {
        font-size: 30px;
      }
    }

    &__info {
      margin-bottom: 107px;
    }

    &__btns {
      margin-bottom: 0;
    }

    &__house {
      right: -220px;
      width: 1270px;
    }
  }

  @include max(1300) {
    &__content {
      max-width: 645px;
    }

    &__title {
      font-size: 22px;

      strong {
        font-size: 34px;
      }

      b {
        font-size: 26px;
      }
    }

    &__house {
      width: 1070px;
    }
  }

  @include max(1200) {
    padding-top: 186px;
  }

  @include max(1024) {
    padding-bottom: 35px;
    min-height: 0;

    &__house {
      right: -80px;
      width: 620px;
    }

    &__btns {
      margin-bottom: 49px;
    }
  }

  @include max(992) {
    padding-top: 76px;

    &__content {
      max-width: 405px;
    }

    &__info {
      margin-bottom: 15px;
    }

    &__title {
      font-size: 18px;

      strong {
        font-size: 24px;
      }

      b {
        font-size: 20px;
      }
    }

    &__btns {
      margin-bottom: 0;
    }

    &__img {
      width: calc(100% + 60px);
    }

    &__house {
      right: -180px;
      width: 700px;
    }
  }

  @include max(768) {
    &__content {
      max-width: 305px;
    }

    &__house {
      bottom: 0;
      width: 600px;
    }
  }

  @include max(490) {
    min-height: 700px;
    padding-top: 115px;
    padding-bottom: 51px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 950px;
      height: 120%;
      background-image: url("../img/clouds.png");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: left top;
    }

    &__info {
      margin-bottom: 258px;
    }

    &__title {
      width: calc(100% + 5px);
      font-size: 20px;

      strong {
        font-size: 22px;
      }

      b {
        font-size: 22px;
      }
    }

    &__inscr {
      margin-bottom: 26px;
    }

    &__btns {
      text-align: center;
    }

    &__content {
      position: relative;
      max-width: 100%;
      z-index: 1;
    }

    &__house {
      right: auto;
      left: 50%;
      bottom: 55px;
      transform: translateX(-50%);
      width: 100%;
      min-width: 476px;
    }

    &__img {
      width: 100%;
    }
  }

  @include max(360) {
    min-height: 600px;
    padding-top: 75px;

    &__info {
      margin-bottom: 208px;
    }
  }
}
